.s-about {
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: rem(40);

    @media (min-width: 992px) {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__photo {
    img {
      display: block;
      width: 100%;
      height: auto;
      max-width: rem(470);
      margin: 0 auto;
    }
  }

  &__text {
    font-family: $primary_font;
  }
}
